// ÂàõÂª∫Â§öÁ´ØÂ∫îÁî®
const createApplication = (params = {
    name : '',
    apply_for : '',
    is_exclusive : false,
    tenant_code : '',
    icon_url : '',
    app_code : '',
    android_id : '',
    ios_id : '',
    ios_deploy_mod : 1,
    description : '',
    type : 'pc',
}) => {
    if (typeof params !== 'object') {
        params = {};
    }
    return POST('app/client/add', params);
};

