      </span>
    </div>
    <!-- ÊúÄÂ§ö6‰∏™Â≠óÁ¨¶ -->
    <el-form-item label="Â∫îÁî®ÂêçÁß∞" prop="name">
      <el-input v-model="form.name" placeholder="ËØ∑ËæìÂÖ•Â∫îÁî®ÂêçÁß∞"></el-input>
    </el-form-item>
    <!-- ‰∏çÂèØÁºñËæë -->
    <el-form-item label="Â∫îÁî®Áî®Êà∑" prop="apply_for">
      <el-radio-group v-model="form.apply_for" :disabled="disabledField.apply_for">
        <el-radio label="Èù¢Âêë‰ºÅ‰∏öÂÜÖÈÉ®Áî®Êà∑"></el-radio>
        <el-radio label="Èù¢Âêë‰ºÅ‰∏ö‰æõÂ∫îÂïÜ‰∏éÁî®Êà∑"></el-radio>
      </el-radio-group>
    </el-form-item>
    <!-- ÈªòËÆ§Âê¶Ôºå‰∏çÂèØÁºñËæë -->
    <el-form-item label="ÊòØÂê¶ÂÆ¢Êà∑‰∏ìÂ±û" prop="is_exclusive">
      <el-radio-group v-model="form.is_exclusive" :disabled="disabledField.is_exclusive">
        <el-radio label="Âê¶"></el-radio>
        <el-radio label="ÊòØ"></el-radio>
      </el-radio-group>
    </el-form-item>
    <!-- APPÊòØÂÆ¢Êà∑‰∏ìÂ±ûÊó∂ÔºåÊòæÁ§∫ËØ•Â≠óÊÆµÔºåÂøÖÂ°´ -->
    <el-form-item label="ÁßüÊà∑‰ª£Á†Å" prop="tenant_code" v-if="form.is_exclusive === 'ÊòØ'">
      <el-input
        v-model="form.tenant_code"
        placeholder="ËØ∑ËæìÂÖ•ÁßüÊà∑‰ª£Á†Å"
        :disabled="disabledField.tenant_code"
      ></el-input>
    </el-form-item>
    <!-- Â∫îÁî®ÂõæÊ†á -->
    <el-form-item label="Â∫îÁî®ÂõæÊ†á">
      <el-upload
        class="avatar-uploader"
        list-type="picture-card"
        action="https://manager-test.mypaas.com/app/index/upload-app-logo"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
        :http-request="uploadImage"
        :on-preview="handlePictureCardPreview"
      >
        <img v-if="form.icon_url" :src="form.icon_url" class="avatar" />
        <i v-else class="el-icon-plus"></i>
      </el-upload>
      <span>Â∞∫ÂØ∏Â§ßÂ∞èÔºö1024*1024 png/jpg/jpegÊ†ºÂºè</span>
    </el-form-item>
    <!-- ÂøÖÂ°´ÔºåÊîØÊåÅÂ≠óÊØç„ÄÅÊï∞Â≠ó„ÄÅ‰∏≠ÂàíÁ∫øÔºå‰∏î‰∏≠ÂàíÁ∫ø‰∏çËÉΩÊîæÂú®È¶ñÂ∞æÔºå‰∏çËÉΩËøûÁª≠Âá∫Áé∞ÔºåÂÖ®Âπ≥Âè∞ÂîØ‰∏ÄÔºå‰∏çÂèØÁºñËæë-->
    <el-form-item label="Â∫îÁî®ÁºñÁ†Å" prop="app_code">
      <el-input
        v-model="form.app_code"
        :disabled="disabledField.app_code"
        placeholder="ËØ∑ËæìÂÖ•Â∫îÁî®ÁºñÁ†ÅÔºå‰øùÂ≠òÂêé‰∏çÂèØÊõ¥Êîπ"
      ></el-input>
    </el-form-item>
    <span class="promptWord">Android</span>
    <!-- ÂøÖÂ°´ÔºåÂèçÂüüÂêçÊ†ºÂºèÊ†°È™åÔºå‰∏çÂèØÁºñËæë -->
    <el-form-item label="ÂåÖÂêç" prop="android_id">
      <el-input
        v-model="form.android_id"
        :disabled="disabledField.android_id"
        placeholder="ËØ∑ËæìÂÖ•ÂåÖÂêçÔºå‰æãÂ¶ÇÔºöcom.mingyuanyun.demo"
      ></el-input>
    </el-form-item>
    <span class="promptWord">ios</span>
    <!-- ÂøÖÂ°´ÔºåÂèçÂüüÂêçÊ†ºÂºèÊ†°È™åÔºå‰∏çÂèØÁºñËæë -->
    <el-form-item label="BundleID" prop="ios_id" class="bundleID">
      <el-input
        v-model="form.ios_id"
        :disabled="disabledField.ios_id"
        placeholder="ËØ∑ËæìÂÖ•BundleIDÔºå‰æãÂ¶ÇÔºöcom.mingyuanyun.demo"
      ></el-input>
    </el-form-item>
    <!-- ÈÄâÂÆöÂêé‰∏çÂèØÊõ¥ÊîπÔºåÈªòËÆ§‰∏∫AppStore -->
    <el-form-item label="ÂèëÂ∏ÉÊñπÂºè" prop="ios_deploy_mod">
      <el-radio-group v-model="form.ios_deploy_mod" :disabled="disabledField.ios_deploy_mod">
        <el-radio label="AppStore"></el-radio>
        <el-radio label="In House"></el-radio>
      </el-radio-group>
      <br />
      <span class="publishPromptWord">{{ publishPromptWord }}</span>
    </el-form-item>
    <!-- ÊúÄÂ§ß200Â≠óÁ¨¶ -->
    <el-form-item label="Â∫îÁî®ÊèèËø∞" prop="description">
      <el-input type="textarea" v-model="form.description" placeholder="ËØ∑ËæìÂÖ•Â∫îÁî®ÊèèËø∞"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit('form')">{{
        this.newCreateAppLabel === true ? 'Á´ãÂç≥ÂàõÂª∫' : '‰øùÂ≠ò‰øÆÊîπ'
      }}</el-button>
      <el-button @click="cancle">ÂèñÊ∂à</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { getSingleAppInfo, createApplication, editSingleAppInfo } from '../api/appManagerApi';
import axios from 'axios';
export default {
  name: 'AppManagerForm',
  props: ['currentEditAppParams', 'createAppLabel'],
  data() {
    return {
      form: {
        name: '', // Â∫îÁî®Âêç
        apply_for: 'Èù¢Âêë‰ºÅ‰∏öÂÜÖÈÉ®Áî®Êà∑', // ÈÄÇÁî®Áî®Êà∑ 1ÔºöÂÜÖÈÉ® 2ÔºöÂ§ñÈÉ®
        is_exclusive: 'Âê¶', // ÊòØÂê¶‰∏ìÂ±ûÂ∫îÁî® 0ÔºöÂê¶ 1ÔºöÊòØ
        tenant_code: '', // ‰∏ìÂ±ûÂ∫îÁî®ÁßüÊà∑‰ª£Á†ÅÔºå‰∏ìÂ±ûÂ∫îÁî®Êó∂ÂøÖÂ°´
        app_code: '', // Â∫îÁî®ÁºñÁ†Å
        android_id: '', // AndroidÂåÖÂêç
        ios_id: '', // iOS Bundle ID
        ios_deploy_mod: 'AppStore', // iOSÂèëÂ∏ÉÊ®°Âºè 1Ôºö‰∏äÊû∂ÂåÖÔºàÈªòËÆ§Ôºâ 2Ôºö‰ºÅ‰∏öÂåÖ
        description: '', // Â∫îÁî®ÊèèËø∞
        type: 'pc', // Â∫îÁî®Á±ªÂûã app(ÈªòËÆ§) pc
        icon_url: '',
        id: '',
      },
      rules: {
        name: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Â∫îÁî®Áî®Êà∑ÂêçÁß∞', trigger: 'blur' },
          { min: 1, max: 6, message: 'ÈïøÂ∫¶ÊúÄÂ§ß‰∏∫6‰∏™Â≠óÁ¨¶', trigger: 'blur' },
        ],
        apply_for: [{ required: true, message: 'ËØ∑ËæìÂÖ•Â∫îÁî®Áî®Êà∑ÂêçÁß∞', trigger: 'blur' }],
        tenant_code: [{ required: true, message: 'ËØ∑ËæìÂÖ•ÁßüÊà∑‰ª£Á†Å', trigger: 'blur' }],
        app_code: [{ required: true, message: 'ËØ∑ËæìÂÖ•Â∫îÁî®ÁºñÁ†Å', trigger: 'blur' }],
        is_exclusive: [{ required: true, message: 'ËØ∑ÈÄâÊã©ÊòØÂê¶ÂÆ¢Êà∑‰∏ìÂ±û', trigger: 'change' }],
        android_id: [{ required: true, message: 'ËæìÂÖ•ÂåÖÂêç', trigger: 'change' }],
        ios_id: [{ required: true, message: 'ËØ∑ËæìÂÖ•BundleID', trigger: 'change' }],
        // ios_deploy_mod: [{ required: true, message: 'ËØ∑ÈÄâÊã©ÂèëÂ∏ÉÊñπÂºè', trigger: 'change' }],
        description: [{ max: 200, message: 'ÈïøÂ∫¶ÊúÄÂ§ß‰∏∫200‰∏™Â≠óÁ¨¶', trigger: 'blur' }],
      },
      disabledField: {
        apply_for: true,
        is_exclusive: true,
        tenant_code: true,
        app_code: true,
        android_id: true,
        ios_id: true,
        ios_deploy_mod: true,
      },

      newCurrentEditAppParams: this.currentEditAppParams,
      newCreateAppLabel: this.createAppLabel,

      dialogImageUrl: '',
    };
  },
  computed: {
    publishPromptWord: function () {
      return this.form.ios_deploy_mod === 'In House'
        ? 'In HouseÊñπÂºèÊåáÈÄöËøáÊåâÁÖßApplÁöÑËßÑËåÉÂ∞ÜiosÂÆâË£ÖÂåÖÊîæÁΩÆÂú®Ëá™Ë°åÂª∫Á´ãÁöÑÁΩëÁ´ô‰∏äÔºåÁî®Êà∑ÈÄöËøáÊµèËßàÁΩëÁ´ôËá™Ë°å‰∏ãËΩΩÂÆâË£ÖÁöÑÊñπÂºèÊù•ÂÆâË£ÖÂ∫îÁî®„ÄÇ'
        : 'AppStoreÂèëÂ∏ÉÊñπÂºèÊåáÂ∫îÁî®ÂàÜÂèëÂà∞ËãπÊûúÂ∫îÁî®ÂïÜÂ∫óÔºåÁî®Êà∑Âè™ËÉΩÈÄöËøáAppStore‰∏ãËΩΩÂÆâË£Ö„ÄÇËØ•ÈÄâÈ°πÈúÄË¶ÅÊèêÂâçÁî≥ËØ∑ËãπÊûúÂºÄÂèëËÄÖË¥¶Âè∑Âπ∂Â∞ÜÁî≥ËØ∑Âà∞ÁöÑÂºÄÂèëËÄÖËØÅ‰π¶‰∏ä‰º†Âà∞Âπ≥Âè∞„ÄÇ';
    },
  },
  watch: {
    currentEditAppParams(newVal) {
      if (!newVal.type) {
        for (let key in this.form) {
          this.form[key] = '';
        }
        console.log(666);
        this.form.apply_for = 'Èù¢Âêë‰ºÅ‰∏öÂÜÖÈÉ®Áî®Êà∑';
        this.form.is_exclusive = 'Âê¶';
        this.form.ios_deploy_mod = 'AppStore';

        this.changeDisabledField();
        return;
      }
      this.newCurrentEditAppParams = newVal;
      this.initDataHandle();
    },
    createAppLabel(newVal) {
      this.newCreateAppLabel = newVal;
    },
  },
  methods: {
    async onSubmit(formName) {
      // Ê†ºÂºèÊ†°È™å
      let isVaild = false;
      this.$refs[formName].validate(valid => {
        if (valid) {
          // ÊúçÂä°ÂïÜ‰ª£Á†ÅÂÅöÁâπÊÆäÊ†°È™å
          isVaild = this.checkAppcode() === true && this.checkDescription() === true ? true : false;
        } else {
          this.notifyMessage('warning');
        }
      });
      if (isVaild) {
        this.form.description = this.form.description.trim();
        this.dataTypeTransformString();
        if (this.newCreateAppLabel) {
          // Êñ∞Âª∫
          if (this.form.is_exclusive === '0') this.form.tenant_code = '';
          let createResult = await createApplication(this.form);
          this.resultStatus(createResult);
        } else {
          // ÁºñËæë‰øùÂ≠ò
          await this.editSave();
        }
      }
    },
    // Êï∞ÊçÆÂàùÂßãÂåñÂ§ÑÁêÜ
    async initDataHandle() {
      if (!this.newCurrentEditAppParams.type) {
        this.changeDisabledField();
        return;
      }
      const currentAPPInfo = await getSingleAppInfo(this.newCurrentEditAppParams);
      this.form = currentAPPInfo.data.data;
      this.dataTypeTransformLabel();
      console.log('form', currentAPPInfo);
    },
    // Â≠óÊÆµÂΩ¢ÂºèËΩ¨Âåñ
    dataTypeTransformLabel() {
      // Â∫îÁî®Áî®Êà∑
      this.form.apply_for = Object.is(this.form.apply_for, '1') ? 'Èù¢Âêë‰ºÅ‰∏öÂÜÖÈÉ®Áî®Êà∑' : 'Èù¢Âêë‰ºÅ‰∏ö‰æõÂ∫îÂïÜ‰∏éÁî®Êà∑';
      // ÊòØÂê¶ÂÆ¢Êà∑‰∏ìÂ±û
      this.form.is_exclusive = Object.is(this.form.is_exclusive, '0') ? 'Âê¶' : 'ÊòØ';
      // ÂèëÂ∏ÉÊñπÂºè
      this.form.ios_deploy_mod = Object.is(this.form.ios_deploy_mod, '1') ? 'AppStore' : 'In House';
    },
    dataTypeTransformString() {
      // Â∫îÁî®Áî®Êà∑
      this.form.apply_for = Object.is(this.form.apply_for, 'Èù¢Âêë‰ºÅ‰∏öÂÜÖÈÉ®Áî®Êà∑') ? '1' : '2';
      // ÊòØÂê¶ÂÆ¢Êà∑‰∏ìÂ±û
      this.form.is_exclusive = Object.is(this.form.is_exclusive, 'Âê¶') ? '0' : '1';
      // ÂèëÂ∏ÉÊñπÂºè
      this.form.ios_deploy_mod = Object.is(this.form.ios_deploy_mod, 'AppStore') ? '1' : '2';
    },
    // ÂõæÁâá‰∏ä‰º†
    handleAvatarSuccess(res) {
      console.log('res', res);
      // this.form.icon_url = URL.createObjectURL(file.raw);
    },
    // ÂõæÁâá‰∏ä‰º†ÂâçÁöÑÂ§ÑÁêÜ
    beforeAvatarUpload(file) {
      const isJPG = file.type in ['image/jpeg', 'image/jpg', 'image/png'];
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('‰∏ä‰º†Â§¥ÂÉèÂõæÁâáÊ†ºÂºèÂè™ÊîØÊåÅ JPG/PNG/JPEG Ê†ºÂºè!');
      }

      if (!isLt2M) {
        this.$message.error('‰∏ä‰º†Â§¥ÂÉèÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá 2MB!');
      }
      return isLt2M;
    },
    changeDisabledField() {
      for (let key in this.disabledField) {
        this.disabledField[key] = false;
      }
    },
    // Ê∂àÊÅØÈÄöÁü•Á±ªÂûã
    notifyMessage(status, res) {
      switch (status) {
        case 'success':
          this.$notify({
            title: 'Êìç‰ΩúÊàêÂäüÔºÅ',
            message: 'ÊÅ≠ÂñúüíêÔºåÊìç‰ΩúÊàêÂäü‰∫ÜÔºÅüòú',
            type: 'success',
          });
          break;
        case 'failure':
          this.$notify.error({
            title: 'Â§±Ë¥•ÔºÅ',
            message: 'Êä±Ê≠âüò≠ÔºåÊìç‰ΩúÂ§±Ë¥•‰∫ÜÔºÅ',
          });
          break;
        case 'warning':
          this.$notify({
            title: 'Ê†°È™åÊú™ÈÄöËøá',
            message: 'Êä±Ê≠âÔºåÊ†°È™åÊú™ÈÄöËøáüòû',
            type: 'warning',
          });
          break;
        case 'message':
          this.$notify.error({
            title: 'Â§±Ë¥•ÔºÅüò±',
            message: `${res?.data?.msg}`,
          });
      }
    },
    resultStatus(res) {
      if (Number(res?.data?.code) === 0) {
        this.notifyMessage('success');
        this.$emit('getAllListData');
        this.$emit('changeDialogVisibleFalse');
      } else if (Number(res?.data?.code) === 400) {
        this.notifyMessage('message', res);
      } else {
        this.notifyMessage('failure');
      }
    },
    cancle() {
      this.$emit('changeDialogVisibleFalse');
    },
    // ÂõæÁâá‰∏ä‰º†
    async uploadImage(req) {
      const config = {
        headers: { 'Content-Type': 'multipart/form-data' },
      };
      const formdata = new FormData();
      formdata.append('__upfile__', req.file);
      axios
        .post('https://manager-test.mypaas.com/app/index/upload-app-logo', formdata, config)
        .then(res => {
          console.log('image upload succeed.');
          console.log('res', res);
          this.form.icon_url = res?.data.result;
        })
        .catch(err => {
          console.log(err.message);
        });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
    },
    // Ê£ÄÊü•ÊúçÂä°ÂïÜ‰ª£Á†Å
    checkAppcode() {
      if (!/^(?!-)[a-zA-Z0-9-]{1,}$/.exec(this.form.app_code)) {
        this.$notify.error({
          title: 'Â§±Ë¥• üò≠',
          message: 'Â∫îÁî®ÁºñÁ†Å‰ªÖÊîØÊåÅËã±ÊñáÂ≠óÊØçÂèä‰∏≠ÂàíÁ∫øÁªÑÂêàÔºå‰∏î‰∏çËÉΩ‰ª•‰∏≠ÂàíÁ∫øÂºÄÂ§¥',
        });
        return false;
      } else {
        return true;
      }
    },
    checkDescription() {
      if (/\s{10}/.exec(this.form.description)) {
        this.$notify.error({
          title: 'Â§±Ë¥• üò≠',
          message: 'ËØ∑ÂãøÂú®Â∫îÁî®ÊèèËø∞‰∏≠ËæìÂá∫Â§ö‰∏™ËøûÁª≠Á©∫Ê†ºüò°',
        });
        return false;
      } else {
        return true;
      }
    },
    // ÁºñËæë‰øùÂ≠ò
    async editSave() {
      let params = {
        name: this.form.name,
        id: this.form.id,
        type: this.form.type,
        description: this.form.description,
        icon_url: this.form.icon_url,
      };
      let res = await editSingleAppInfo(params);
      this.resultStatus(res);
      this.$emit('changeCreateAppLabelTrue');
    },
  },
  async created() {
    this.initDataHandle();
  },
  mounted() {},
};
</script>

